// IMPORTS
// Material UI
import AppBar from '@material-ui/core/AppBar'
import Dialog from '@material-ui/core/Dialog'
import IconButton from '@material-ui/core/IconButton'
import Slide from '@material-ui/core/Slide'
import Toolbar from '@material-ui/core/Toolbar'
import Typography from '@material-ui/core/Typography'
import withStyles from '@material-ui/core/styles/withStyles'
import CloseIcon from '@material-ui/icons/Close'

// Externals imports (Dependencies)
import React from 'react'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux'
import PropTypes from 'prop-types'

// Internal imports
import EnhancedComponent from '../../components/EnhancedComponent'
import { edit, fetchStores, update } from '../StoreActions'
import { getEditingStore } from '../StoreReducer'
import styles from '../../../resources/theme/stores'
import Form from './Form'

function Transition(props) {
  return <Slide direction="down" {...props} />
}

class EditStore extends EnhancedComponent {
  state = {
    fields: {
      name: '',
      email: '',
      role_id: '',
    },
  }

  componentDidMount() {
    this.props.edit(this.props.match.params.id)
  }

  onSubmit = values => {
    const { fetchUsers } = this.props
    // valida os campos se necessário
    this.props.update(values).then(data => {
      fetchUsers()
      if (data && data.code === 200) {
        this.props.history.push('/users')
      }
    })
  }

  onClose = () => {
    this.props.history.push('/users')
  }

  render() {
    const { classes, user } = this.props
    return (
      <Dialog
        open={user !== null}
        onClose={this.onClose}
        TransitionComponent={Transition}
      >
        <AppBar className={classes.appBar}>
          <Toolbar>
            <Typography variant="h6" color="inherit" className={classes.flex}>
              Editar Usuário
            </Typography>
            <IconButton
              color="inherit"
              onClick={this.onClose}
              aria-label="Close"
            >
              <CloseIcon />
            </IconButton>
          </Toolbar>
        </AppBar>

        <Form data={user} onClose={this.onClose} onSubmit={this.onSubmit} />
      </Dialog>
    )
  }
}

EditUser.propTypes = {
  classes: PropTypes.object.isRequired,
  history: PropTypes.object.isRequired,
  roles: PropTypes.array.isRequired,
}

const mapStateToProps = state => ({
  roles: state.user.roles || [],
  user: getEditingUser(state),
})

const mapDispatchToProps = dispatch =>
  bindActionCreators(
    {
      update,
      edit,
      fetchUsers,
    },
    dispatch
  )

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(withStyles(styles)(EditUser))
